import ServiceScope from '../../serviceScope/ServiceScope';
import { ITimeProvider} from '../../common/TimeProvider';

/**
 * This is a test implementation of ITimeProvider that allows unit tests
 * to simulate the system clock.
 */
export default class MockTimeProvider implements ITimeProvider {
  /**
   * The time value that will be returned by getNowMs().
   */
  public currentDate: Date;
  public currentTimestamp: number;

  constructor(serviceScope: ServiceScope) {
    // Pick an arbitrary starting point of 9am on Jan 1, 2010
    this.currentDate = new Date(2010, 1, 1, 9);
    // Pick an arbitrary starting time of 100 seconds.
    this.currentTimestamp = 100 * 1000;
  }

  /** Implements the ITimeProvider interface. */
  public getDate(): Date {
    return this.currentDate;
  }

  /** Implements the ITimeProvider interface. */
  public getTimestamp(): number {
    return this.currentTimestamp;
  }

  /**
   * Advances the simulated clock by the specified number of milliseconds.
   */
  public addMs(milliseconds: number): void {
    this.currentDate = new Date(this.currentDate.valueOf() + milliseconds);
    this.currentTimestamp += milliseconds;
  }
}
